.row {
	display: flex;
	flex-direction: row;
}

.Calculator {
	margin-top: 5vh;
	margin-left: 40vw;
	background-image: url(/react-calculator/static/media/iphone.6ae6785e.png);
	background-size: 325px;
	background-repeat: no-repeat;
	width: 325px;
	height: 95vh;
	padding-bottom: 0;
	margin-bottom: 0;
}

.innerElements {
	padding-top: 80px;
	padding-left: 50px;
}

button {
	background-color: #444448; /* Background */
	border: none;
	outline: none;
	border-radius: 30px;
	font-family: 'Work Sans', sans-serif;
	font-weight: 300;
	font-size: 22px;
	color: white;
	height: 45px;
	width: 45px;
	margin: 6px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	
	-webkit-transition: background-color 0.25s ease;
	
	transition: background-color 0.25s ease;
}

button:hover {
	background-color: #FF6B6B;
	cursor: pointer;
}

button:active {
	background-color: #D15858;
}
.Output {
	width: 230px;
	margin-bottom: 50px;

}

h1 {
	text-align: right;
	padding-right: 10px;
	color: white;
	font-family: 'Work Sans', sans-serif;
	font-weight: 300;
